import React, { useState, useEffect } from 'react'
import { useCMS } from '../contexts/CMSContext'

interface CMSEditorProps {
    isOpen: boolean
    onClose: () => void
}

export const CMSEditor: React.FC<CMSEditorProps> = ({ isOpen, onClose }) => {
    const { cmsData, resetCMSData, exportCMSData, importCMSData } = useCMS()
    const [activeTab, setActiveTab] = useState<'global' | 'pages'>('global')
    const [activePage, setActivePage] = useState<keyof typeof cmsData.pages>('home')
    const [exportData, setExportData] = useState('')
    const [importData, setImportData] = useState('')

    useEffect(() => {
        if (isOpen) {
            setExportData(exportCMSData())
        }
    }, [isOpen, exportCMSData])

    const handleExport = () => {
        const dataStr = exportCMSData()
        const dataBlob = new Blob([dataStr], { type: 'application/json' })
        const url = URL.createObjectURL(dataBlob)
        const link = document.createElement('a')
        link.href = url
        link.download = 'sbu-cms-data.json'
        link.click()
        URL.revokeObjectURL(url)
    }

    const handleImport = () => {
        if (importCMSData(importData)) {
            alert('Data imported successfully!')
            setImportData('')
        } else {
            alert('Failed to import data. Please check the JSON format.')
        }
    }

    const handleReset = () => {
        if (confirm('Are you sure you want to delete all draft changes and restore to original? This cannot be undone.')) {
            resetCMSData()
            alert('All draft changes deleted! Data restored to original successfully!')
        }
    }

    if (!isOpen) return null

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center">
            <div className="bg-white rounded-lg shadow-xl w-11/12 h-5/6 max-w-6xl flex flex-col">
                {/* Header */}
                <div className="flex items-center justify-between p-4 border-b">
                    <h2 className="text-xl font-bold">CMS Editor</h2>
                    <button
                        onClick={onClose}
                        className="text-gray-500 hover:text-gray-700 text-2xl"
                    >
                        Ã—
                    </button>
                </div>

                {/* Tabs */}
                <div className="flex border-b">
                    <button
                        onClick={() => setActiveTab('global')}
                        className={`px-4 py-2 font-medium ${
                            activeTab === 'global'
                                ? 'border-b-2 border-blue-500 text-blue-600'
                                : 'text-gray-600 hover:text-gray-800'
                        }`}
                    >
                        Global Settings
                    </button>
                    <button
                        onClick={() => setActiveTab('pages')}
                        className={`px-4 py-2 font-medium ${
                            activeTab === 'pages'
                                ? 'border-b-2 border-blue-500 text-blue-600'
                                : 'text-gray-600 hover:text-gray-800'
                        }`}
                    >
                        Pages
                    </button>
                </div>

                {/* Content */}
                <div className="flex-1 flex overflow-hidden">
                    {/* Sidebar */}
                    {activeTab === 'pages' && (
                        <div className="w-64 border-r bg-gray-50 overflow-y-auto">
                            <div className="p-4">
                                <h3 className="font-medium text-gray-900 mb-2">Pages</h3>
                                {Object.keys(cmsData.pages).map((pageKey) => (
                                    <button
                                        key={pageKey}
                                        onClick={() => setActivePage(pageKey as keyof typeof cmsData.pages)}
                                        className={`w-full text-left px-3 py-2 rounded mb-1 ${
                                            activePage === pageKey
                                                ? 'bg-blue-100 text-blue-700'
                                                : 'hover:bg-gray-100'
                                        }`}
                                    >
                                        {pageKey === 'home' ? 'Home' : 
                                         pageKey === 'page2' ? 'About SBU' :
                                         pageKey === 'page3' ? 'Meditation' :
                                         pageKey === 'page4' ? 'Elementary Program' :
                                         pageKey === 'page5' ? 'College Program' :
                                         pageKey === 'page6' ? 'Abhidhamma Program' : pageKey}
                                    </button>
                                ))}
                            </div>
                        </div>
                    )}

                    {/* Main Content */}
                    <div className="flex-1 overflow-y-auto p-6">
                        {activeTab === 'global' && (
                            <div className="space-y-6">
                                <div>
                                    <h3 className="text-lg font-medium mb-4">Data Management</h3>
                                    <div className="space-y-4">
                                        <div className="flex flex-wrap gap-2">
                                            <button
                                                onClick={handleExport}
                                                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
                                            >
                                                Export Data
                                            </button>
                                            <button
                                                onClick={handleReset}
                                                className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
                                            >
                                                Delete Draft
                                            </button>
                                        </div>
                                        
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                                Import Data (JSON)
                                            </label>
                                            <textarea
                                                value={importData}
                                                onChange={(e) => setImportData(e.target.value)}
                                                className="w-full h-32 p-3 border border-gray-300 rounded-md"
                                                placeholder="Paste JSON data here..."
                                            />
                                            <button
                                                onClick={handleImport}
                                                disabled={!importData.trim()}
                                                className="mt-2 bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 disabled:bg-gray-300"
                                            >
                                                Import Data
                                            </button>
                                        </div>

                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                                Current Data (Read-only)
                                            </label>
                                            <textarea
                                                value={exportData}
                                                readOnly
                                                className="w-full h-64 p-3 border border-gray-300 rounded-md bg-gray-50 font-mono text-sm"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )}

                        {activeTab === 'pages' && (
                            <div>
                                <h3 className="text-lg font-medium mb-4">
                                    Editing: {activePage === 'home' ? 'Home' : 
                                             activePage === 'page2' ? 'About SBU' :
                                             activePage === 'page3' ? 'Meditation' :
                                             activePage === 'page4' ? 'Elementary Program' :
                                             activePage === 'page5' ? 'College Program' :
                                             activePage === 'page6' ? 'Abhidhamma Program' : activePage}
                                </h3>
                                
                                <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4 mb-4">
                                    <p className="text-yellow-800">
                                        <strong>Live Preview:</strong> Changes are automatically saved to local storage 
                                        and will be reflected on the website immediately. Use the browser's developer 
                                        tools to inspect the current data structure.
                                    </p>
                                </div>

                                <div className="bg-gray-50 border border-gray-200 rounded-md p-4">
                                    <h4 className="font-medium mb-2">Page Data Structure</h4>
                                    <pre className="text-sm overflow-auto max-h-96 bg-white p-3 rounded border">
                                        {JSON.stringify(cmsData.pages[activePage], null, 2)}
                                    </pre>
                                </div>
                            </div>
                        )}
                    </div>
                </div>

                {/* Footer */}
                <div className="border-t p-4 bg-gray-50">
                    <div className="flex items-center justify-between">
                        <div className="text-sm text-gray-600">
                            Data is automatically saved to localStorage
                        </div>
                        <div className="text-sm text-gray-600">
                            Last updated: {new Date().toLocaleString()}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}