import React from 'react'
import { CMSEditor } from './CMSEditor'
import { useCMSEditor } from './useLivePreview'

export const CMSEditorButton: React.FC = () => {
    const { isEditorOpen, openEditor, closeEditor, isEditorMode } = useCMSEditor()

    if (!isEditorMode) return null

    return (
        <>
            {/* Floating Editor Button */}
            <button
                onClick={openEditor}
                className="fixed bottom-4 right-4 z-40 bg-blue-600 hover:bg-blue-700 text-white p-3 rounded-full shadow-lg transition-colors"
                title="Open CMS Editor"
            >
                <svg 
                    width="24" 
                    height="24" 
                    viewBox="0 0 24 24" 
                    fill="none" 
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path 
                        d="M3 17.25V21h3.75L17.81 9.94l-3.75-3.75L3 17.25zM20.71 7.04c.39-.39.39-1.02 0-1.41l-2.34-2.34c-.39-.39-1.02-.39-1.41 0l-1.83 1.83 3.75 3.75 1.83-1.83z" 
                        fill="currentColor"
                    />
                </svg>
            </button>

            {/* CMS Editor Modal */}
            <CMSEditor isOpen={isEditorOpen} onClose={closeEditor} />
        </>
    )
}