import React from 'react'
import { CMSProvider } from '../contexts/CMSContext'
import { CMSEditorButton } from './CMSEditorButton'

interface CMSLayoutProps {
    children: React.ReactNode
}

/**
 * Layout wrapper that provides CMS functionality to the entire application
 * This should wrap your main App component or layout
 */
export const CMSLayout: React.FC<CMSLayoutProps> = ({ children }) => {
    return (
        <CMSProvider>
            {children}
            <CMSEditorButton />
        </CMSProvider>
    )
}

/**
 * Higher-order component for pages that need CMS integration
 */
export const withCMS = <P extends object>(Component: React.ComponentType<P>) => {
    const WrappedComponent: React.FC<P> = (props) => {
        return (
            <CMSProvider>
                <Component {...props} />
                <CMSEditorButton />
            </CMSProvider>
        )
    }
    
    WrappedComponent.displayName = `withCMS(${Component.displayName || Component.name})`
    return WrappedComponent
}