import React, { useEffect, useState } from "react";
import "../global.css";
import Header from "../components/header/header";
import UpcomingEvent from "../components/UpcomingEvent/UpcomingEvent";
import Announcements from "../components/news/news";
import Programs from "../components/program/program";
import DharmaSupport from "../components/DharmaSupport/DharmaSupport";
import LibrarySection from "../components/library/library";
import Footer from "../components/Footer/Footer";
import { CMSLayout } from "../components/CMSLayout";

/**
 * Gate the whole page behind a "mounted" flag
 * so Gatsby never runs any of this on the server.
 */
const IndexPageWithCMS = () => {
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  // ----- IMPORTANT FIX -----
  // If we are not in the browser yet, render NOTHING.
  if (!mounted) {
    return null;
  }

  // Once mounted = true, this runs only in the browser.
  return (
    <CMSLayout>
      <div>
        <Header />
        <UpcomingEvent />
        <Announcements />
        <Programs />
        <DharmaSupport />
        <LibrarySection />
        <Footer />
      </div>
    </CMSLayout>
  );
};

export default IndexPageWithCMS;