import { useEffect, useState } from 'react'
import { useCMS } from '../contexts/CMSContext'
import type { CMSData } from '../contexts/CMSContext'

/**
 * Hook for live preview functionality
 * Automatically updates components when CMS data changes
 */
export const useLivePreview = () => {
    const { cmsData } = useCMS()
    const [isPreviewMode, setIsPreviewMode] = useState(false)
    const [lastUpdated, setLastUpdated] = useState<Date>(new Date())

    useEffect(() => {
        // Listen for CMS data updates
        const handleCMSUpdate = (event: CustomEvent<CMSData>) => {
            setLastUpdated(new Date())
            // Force re-render by updating state
            setIsPreviewMode(prev => !prev)
        }

        window.addEventListener('cms-data-updated', handleCMSUpdate as EventListener)

        return () => {
            window.removeEventListener('cms-data-updated', handleCMSUpdate as EventListener)
        }
    }, [])

    return {
        cmsData,
        lastUpdated,
        isPreviewMode
    }
}

/**
 * Hook for detecting if we're in development/editor mode
 */
export const useEditorMode = () => {
    const [isEditorMode, setIsEditorMode] = useState(false)

    useEffect(() => {
        // Check if we're in development mode or if editor is enabled
        const isDev = process.env.NODE_ENV === 'development'
        const hasEditorFlag = typeof window !== 'undefined' && 
                             (window.location.search.includes('editor=true') || 
                              localStorage.getItem('cms-editor-enabled') === 'true')
        
        setIsEditorMode(isDev || hasEditorFlag)
    }, [])

    const toggleEditor = () => {
        if (typeof window !== 'undefined') {
            const newState = !isEditorMode
            setIsEditorMode(newState)
            localStorage.setItem('cms-editor-enabled', newState.toString())
        }
    }

    return {
        isEditorMode,
        toggleEditor
    }
}

/**
 * Hook for managing CMS editor state
 */
export const useCMSEditor = () => {
    const [isEditorOpen, setIsEditorOpen] = useState(false)
    const { isEditorMode } = useEditorMode()

    const openEditor = () => setIsEditorOpen(true)
    const closeEditor = () => setIsEditorOpen(false)

    return {
        isEditorOpen,
        openEditor,
        closeEditor,
        isEditorMode
    }
}